	}
}

[data-isotope-layout] {
	z-index: 0;
	display: block;
	overflow: hidden;
	min-height: 160px;
	margin-left: -15px;
	margin-right: -15px;
	transition: .4s all ease;
}

[data-isotope-layout]:after {
	content: '';
	position: absolute;
	top: 50%;
	left: 50%;
	width: 64px;
	height: 64px;
	margin-top: 15px;
	background-image: url("../images/isotope-loader.png");
	background-position: -1152px 0;
	animation: 0.7s sprite-animation steps(18) infinite;
	transition: .4s all ease;
	transform: translate3d(-50%, -50%, 0);
